.active {
  background-color: #1d4ed8;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #0f172a;
  color: white;
  position: sticky;
  top: 0;
  padding-right: 5%;
}

nav .title {
  font-size: 1.5rem;
  margin: 1rem;
  font-weight: bold;
  text-decoration: none;
  color: white;
}

nav ul {
  display: flex;
  padding: 0px;
  margin: 0px;
}

nav ul li {
  list-style: none;
}

nav ul li a {
  display: block;
  text-decoration: none;
  color: white;
  padding: 0.5rem;
  margin: 0 0.5rem;
  border-radius: 0.5rem;
}

nav ul li a:not(.active):hover {
  background-color: #172554;
}

nav .menu {
  display: none;
  position: absolute;
  top: 0.75rem;
  margin-top: 5px;
  right: 0.5rem;
  flex-direction: column;
  justify-content: space-between;
  width: 2.25rem;
  height: 2rem;
  cursor: pointer;
}

nav .menu span {
  height: 0.4rem;
  width: 100%;
  background-color: #fff;
  border-radius: 0.2rem;
}

.login-div,
.signup-div,
.url-div {
  border: 2px solid black;
  border-radius: 10px;
  margin-top: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 30px;
  padding-bottom: 30px;
}

.err-div {
  color: red;
}

@media (max-width: 880px) {
  nav .menu {
    display: flex;
  }

  nav {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
  }

  nav ul {
    display: none;
    flex-direction: column;
    width: 100%;
    margin-bottom: 0.25rem;
  }

  nav ul.open {
    display: flex;
  }

  nav ul li {
    width: 100%;
    text-align: center;
  }

  nav ul li a {
    margin: 0.2rem 0.5rem;
  }
}

h1 {
  margin-bottom: 50px;
  font-family: "Helvetica", sans-serif;
}

@media (max-width: 300px) {
  nav .title {
    font-size: 1.3rem;
    margin: 1rem;
    font-weight: bold;
    text-decoration: none;
    color: white;
  }
}
